import React from 'react';
import style from './Post.module.css';

type PropsType = {
    message: string
    likesCount: number
}

const Post = (props: PropsType) => {
    return (
        <div className={style.item}>
            <img src='data:image/jpeg;base64,' alt='avatar'/>
            {props.message}
            <div>
            <span>like</span> {props.likesCount}
            </div>
        </div>
    )
};

export default Post;